import { ReactComponent as StarFillIcon } from "../icons/starFill.svg";
import { ReactComponent as StarEmptyIcon } from "../icons/starEmpty.svg";

const Rating = ({ rating }) => {
  return (
    <div className="flex gap-1">
      {Array(5)
        .fill()
        .map((_, idx) =>
          idx < Number(5 - rating) ? (
            <StarEmptyIcon key={idx} />
          ) : (
            <StarFillIcon key={idx} />
          )
        )}
    </div>
  );
};

export default Rating;
