import React from "react";
import { LazyLoadImage } from "react-lazy-load-image-component";
import Rating from "./Rating";
import { ReactComponent as NotePadIcon } from "../icons/notepad.svg";
import TextWithNiceEffect from "./TextWithNiceEffect";
import CustomButton2 from "./CustomButton2";
import { useNavigate } from "react-router-dom";
const TestImage = require('../images/TEST.png')

const ShowSquare = ({ count, title }) => {
  return (
    <div
      className="p-2 rounded-xl"
      style={{ boxShadow: "inset 0px 0px 8px -4px #061628" }}
    >
      <p>{count}</p>
      <p>{title}</p>
    </div>
  );
};

const TrainerCardView = ({ id_TEST }) => {
  const navigate = useNavigate();
  return (
    <div className=" bg-white p-4 text-one rounded-sm max-w-[300px] mx-auto">
      <div className="flex items-center justify-center">
        <LazyLoadImage
          src={TestImage}
          alt="trainer"
          className="w-[110px] h-[110px]"
        />
      </div>
      <div className="flex justify-between mt-4 ">
        <div className="">
          <div className="text-lg font-semibold">Full Name</div>
          <div className="text-sm">Service type</div>
        </div>
        <div>
          <div className="flex items-center gap-2 mt-1 ">
            <div>
              <Rating rating={4} />
            </div>
            <div className="text-sm">(213)</div>
          </div>
          <div />
        </div>
      </div>
      <div className="grid grid-cols-3 mt-2 gap-4 sm:gap-1 md:gap-4 text-center text-[12px]">
        <ShowSquare count={4} title="Work Experience" />
        <ShowSquare count={32} title="Completed Workouts" />
        <ShowSquare count={13} title="Active Clients" />
      </div>
      <div className="flex justify-between items-center mt-6">
        <CustomButton2
          title="Book"
          className="text-sm font-normal "
        />
        <div className=" self-end">
          <TextWithNiceEffect onClick={() => navigate(`/trainer/${id_TEST}`)}>
            <div className="p-[2px] flex items-center gap-1 text-three text-sm">
              <NotePadIcon className="w-5 h-5" />
              <p>More Details</p>
            </div>
          </TextWithNiceEffect>
        </div>
      </div>
    </div>
  );
};

export default TrainerCardView;
