import React from "react";

const TextWithNiceEffect = ({ children, onClick }) => {
  return (
    <button
      onClick={onClick}
      type="button"
      className={`after:bg-three relative after:absolute after:w-0 after:h-[1px]  hover:after:w-full after:transition-all after:duration-300 after:bottom-0 after:left-0`}
    >
      {children}
    </button>
  );
};

export default TextWithNiceEffect;
