import React from "react";

const CustomButton2 = ({ className, onClick, title }) => {
  return (
    <button
      onClick={onClick}
      className={`${className} bg-one text-white px-10 py-2 rounded-xl border-2 border-one hover:bg-white hover:text-one active:shadow-custom-4 transition-custom duration-700 font-semibold`}
    >
      {title}
    </button>
  );
};

export default CustomButton2;
