import React from "react";
import TrainerCardView from "./TrainerCardView";

const TrainersList = () => {
  return (
    <div className=" pt-10">
      <div className="gap-4 lg:gap-4 grid grid-cols-1 sm:grid-cols-2 lg:grid-cols-3 xl:grid-cols-4 ">
        {Array(10)
          .fill()
          .map((_, idx) => (
            <TrainerCardView key={idx} id_TEST={idx} />
          ))}
      </div>
    </div>
  );
};

export default TrainersList;
